.container_1 {
    display: grid;
    grid-template:
      "name exp_1" 1fr
      "passport nationality" 1fr
      / 1fr 1fr;
    grid-gap: 10px;
    color: #FFF;
    font-weight: bold;
    text-align: center;
  }

.name {
    grid-area: name;
}

.exp_1 {
    grid-area: exp_1;
}

.passport {
    grid-area: passport;
}

.nationality {
    grid-area:nationality;
}

.container_2 {
    display: grid;
    grid-template:
      "vat emp_2" 1fr
      "pbox pbox" 1fr
      "product quantity" 1fr
      "value enter" 1fr
      / 1fr 1fr;
    grid-gap: 10px;
    color: #FFF;
    font-weight: bold;
    text-align: center;
  }

.vat {
    grid-area:vat;
}
.emp_2{
    grid-area:emp_2
}

.pbox {
    grid-area:pbox;
}

.product {
    grid-area:product;
}

.value {
    grid-area:value;
}

.enter {
    grid-area:enter;
}
font{
    color:#654018;
}
